import { IDataTypeSerializerFactory, IDataTypeSerializer, DefaultDataTypeSerializerFactory } from '@ecp-caf/common-structure';
import { DefaultGspComponentDataTypeSerializer } from '../serializer/default-gsp-component-data-type-serializer';
import { GspComponentConstant } from '../../../define/gsp-component-constant';

export class DefaultGspComponentDataTypeSerializerFactory implements IDataTypeSerializerFactory {

    private serializer: DefaultGspComponentDataTypeSerializer;
    
    get(kind: string): IDataTypeSerializer {
        switch (kind) {
            case  GspComponentConstant.CMP_STRUCTURED_TYPE_REF:
                if (!this.serializer) {
                    this.serializer = new DefaultGspComponentDataTypeSerializer();
                }
                return this.serializer;
            default:
                return new DefaultDataTypeSerializerFactory().get(kind);
        }
    }

}