import { IGspComponentSerializerFactory } from '../../extension/factory/i-gsp-component-serializer-factory';
import { IGspComponentSerializer } from '../../extension/serializer/i-gsp-component-serializer';
import { DefaultGspComponentSerializer } from '../serializer/default-gsp-component-serializer';

export class DefaultGspComponentSerializerFactory implements IGspComponentSerializerFactory {
    private serializerDict: { [key: string]: IGspComponentSerializer } = {};

    get(kind: string): IGspComponentSerializer {
        switch (kind) {
            case "Default":
                if (!this.serializerDict[kind]) {
                    var serializer = new DefaultGspComponentSerializer();
                    this.serializerDict[kind] = serializer;
                }
                break;
            default:
                throw new Error(`æªå¹éå°ç±»åä¸º${kind}çåºååå¨!`);

        }
        return this.serializerDict[kind];
    }

}