import { IDataType, ISerializeContext } from '@ecp-caf/common-structure';
import { StructureReferType } from '../../define/data-type/structure-refer-type';

export class StructureReferTypeConvertor{

    convertToObject(obj:Object,datatype:IDataType,ctxt:ISerializeContext):void{
        var referType=datatype as StructureReferType;
        obj["kind"]=referType.getKind();
        obj['referId']=referType.referId;
        obj['code']=referType.code;
        obj['name']=referType.name;
    }

    initFromObject(dataType:IDataType,obj:Object,ctxt:ISerializeContext):void{
        var refType=dataType as StructureReferType;
        refType.kind=obj['kind'];
        refType.referId=obj['referId'];
        refType.code=obj['code'];
        refType.name=obj['name'];
    }
}