import { IGspComponentSerializer } from '../../extension/serializer/i-gsp-component-serializer';
import { GspComponent } from '../../../define/gsp-component';
import { IGspComponentSerializeContext } from '../../extension/i-gsp-component-serialize-context';
import { GspComponentConvertor } from '../../../convertor/gsp-component-convertor';

export class DefaultGspComponentSerializer implements IGspComponentSerializer {
    serialize(component: GspComponent, ctxt: IGspComponentSerializeContext): Object {
        let result = new Object();
        //ä½¿ç¨åºæ¬çåºååå¨
        let baseConvertor = new GspComponentConvertor();
        baseConvertor.convertToObject(result, component, ctxt);
        //Cando:å¢å èªå·±å¯¹Objectçæ©å±
        return result;
    }    

    deserialize(obj: Object, ctxt: IGspComponentSerializeContext):GspComponent {
        let result=new GspComponent();
        let baseConvertor=new GspComponentConvertor();
        baseConvertor.initFromObject(result,obj,ctxt);
        return result;
    }


}