import { IGspComponentSerializeContext } from '../extension/i-gsp-component-serialize-context';
import { IDataTypeSerializerFactory, IOperationSerializerFactory, IParameterSerializerFactory, IPropertySerializerFactory, DefaultDataTypeSerializerFactory, DefaultOperationSerializerFactory, DefaultParameterSerializerFactory, DefaultPropertySerializerFactory } from '@ecp-caf/common-structure';
import { DefaultGspComponentDataTypeSerializerFactory } from './factory/default-gsp-component-data-type-serializer-factory';


export class DefaultGspComponentSerializeContext implements IGspComponentSerializeContext {
    private dataTypeFactory: IDataTypeSerializerFactory;
    private operationFactory: IOperationSerializerFactory;
    private parameterFactory: IParameterSerializerFactory;
    private propertyFactory: IPropertySerializerFactory;
    // private gspComponentFactory: IGspComponentSerializerFactory;

    getDataTypeSerializerFactory(): IDataTypeSerializerFactory {
        if (!this.dataTypeFactory) {
            this.dataTypeFactory = new DefaultGspComponentDataTypeSerializerFactory();
        }
        return this.dataTypeFactory;
    }

    getOperationSerializerFactory(): IOperationSerializerFactory {
        if (!this.operationFactory) {
            this.operationFactory = new DefaultOperationSerializerFactory();
        }
        return this.operationFactory;
    }

    getParameterSerializerFactory(): IParameterSerializerFactory {
        if (!this.parameterFactory) {
            this.parameterFactory = new DefaultParameterSerializerFactory();
        }
        return this.parameterFactory;
    }

    getPropertySerializerFactory(): IPropertySerializerFactory {
        if (!this.propertyFactory) {
            this.propertyFactory = new DefaultPropertySerializerFactory();
        }
        return this.propertyFactory;
    }

    // getGspComponentSerializerFactory(): IGspComponentSerializerFactory {
    //     if (!this.gspComponentFactory) {
    //         this.gspComponentFactory = new DefaultGspComponentSerializerFactory();
    //     }
    //     return this.gspComponentFactory;
    // }

}