import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map, mergeMap } from 'rxjs/operators';
import { DefaultSerializeContext, IDataType, CommonStructureDTService, Operation, CollectionType, MapType, EntityObject, CommonStructure, StructuredType, DataType, PrimitiveType } from '@ecp-caf/common-structure';
import { DefaultGspComponentSerializer } from '../serialize-context/default/serializer/default-gsp-component-serializer';
import { DefaultGspComponentSerializeContext } from '../serialize-context/default/default-gsp-component-serialize-context';
import { GspComponent } from '../define/gsp-component';
import { StructureReferType } from '../define/data-type/structure-refer-type';
import { RefStructureInfo } from '../define/ref-structure-info';


@Injectable()
export class CommonComponentService {

    private commom_component_url = '/api/runtime/sys/v1.0/common-component';


    constructor(private http: HttpClient, private structureSvc: CommonStructureDTService) { }


    getComponent(componentId: string, headers: HttpHeaders): Observable<any> {
        let url = this.commom_component_url + '?componentId=' + componentId;
        return this.http.get(url, { headers: headers }).pipe(map(cmpObj => {
            let serializer: DefaultGspComponentSerializer = new DefaultGspComponentSerializer();
            let ctxt: DefaultGspComponentSerializeContext = new DefaultGspComponentSerializeContext();
            let cmp: GspComponent = serializer.deserialize(cmpObj, ctxt);
            return cmp;
        }))
    }

    /**
     * è·åæä»¶ä¸­æä½åæ°çSchema
     * @param componentId æä»¶Id
     * @param operationCode æä½ç¼å·
     * @param paramCode åæ°ç¼å·
     * @param headers è¯·æ±headerï¼åºåå«sessionIdç­å¿éåæ°
     */
    getParamSchema(componentId: string, operationCode: string, paramCode: string, headers: HttpHeaders): Observable<any> {
        let url = this.commom_component_url + '?componentId=' + componentId;
        return this.http.get<any>(url, { headers: headers }).pipe(mergeMap(cmpObj => {
            let serializer: DefaultGspComponentSerializer = new DefaultGspComponentSerializer();
            let ctxt: DefaultGspComponentSerializeContext = new DefaultGspComponentSerializeContext();
            let cmp: GspComponent = serializer.deserialize(cmpObj, ctxt);
            let op: Operation = cmp.operations.find(x => x.code == operationCode);
            if (op == null) {
                throw new Error(`CommonComponent Operation NotFoundï¼OperationCode: ${operationCode}`);
            }
            let param = op.parameters.find(x => x.code == paramCode);
            if (param == null) {
                throw new Error(`CommonComponent Parameter NotFoundï¼ParameterCode: ${paramCode}`);
            }
            let paramType = param.parameterType;
            let result: IDataType;
            //å¼ç¨ç±»å
            if (paramType instanceof StructureReferType) {
                return this.getStructureReferType(cmp, paramType, headers);
            }
            //åè¡¨ç±»å
            else if (paramType instanceof CollectionType) {
                return this.getCollectionType(cmp, paramType, headers);
            }
            //å­å¸ç±»å
            else if (paramType instanceof MapType) {
                return this.getMapType(cmp, paramType, headers);
            }
            //åºæ¬ç±»å
            else {
                return of(paramType);
            }
        }));
    };

    /**
     * è·åæä»¶ä¸­æä½è¿åå¼çSchema
     * @param componentId æä»¶Id
     * @param operationCode æä½ç¼å·
     * @param headers è¯·æ±headerï¼åºåå«sessionIdç­å¿éåæ°
     */
    getReturnValueSchema(componentId: string, operationCode: string, headers: HttpHeaders): Observable<any> {
        let url = this.commom_component_url + '?componentId=' + componentId;
        return this.http.get<any>(url, { headers: headers }).pipe(mergeMap(cmpObj => {
            let serializer: DefaultGspComponentSerializer = new DefaultGspComponentSerializer();
            let ctxt: DefaultGspComponentSerializeContext = new DefaultGspComponentSerializeContext();
            let cmp: GspComponent = serializer.deserialize(cmpObj, ctxt);
            let op: Operation = cmp.operations.find(x => x.code == operationCode);
            if (op == null) {
                throw new Error(`CommonComponent Operation NotFoundï¼OperationCode: ${operationCode}`);
            }
            let returnType = op.returnType;
            let result: IDataType;
            //å¼ç¨ç±»å
            if (returnType instanceof StructureReferType) {
                return this.getStructureReferType(cmp, returnType, headers);
            }
            //åè¡¨ç±»å
            else if (returnType instanceof CollectionType) {
                return this.getCollectionType(cmp, returnType, headers);
            }
            //å­å¸ç±»å
            else if (returnType instanceof MapType) {
                return this.getMapType(cmp, returnType, headers);
            }
            //åºæ¬ç±»å
            else {
                return of(returnType);
            }
        }
        ));
    }


    private getCollectionType(cmp: GspComponent, dataType: CollectionType, headers: HttpHeaders): Observable<CollectionType> {
        let elementType = dataType.elementType;
        if (elementType instanceof StructureReferType) {
            return this.getStructureReferType(cmp, elementType, headers).pipe(map(structuredType => {
                dataType.elementType = structuredType
                return dataType;
            }))
        }
        else if (elementType instanceof PrimitiveType) {
            return of(dataType);
        }
        else {
            throw new Error(`ä¸æ¯æçåè¡¨é¡¹ç±»å: ${elementType.getKind()}`);
        }
    }


    private getMapType(cmp: GspComponent, dataType: MapType, headers: HttpHeaders): Observable<MapType> {
        let valueType = dataType.valueType;
        if (valueType instanceof StructureReferType) {
            return this.getStructureReferType(cmp, valueType, headers).pipe(map(structuredType => {
                dataType.valueType = structuredType;
                return dataType;
            }))
        }
        else if (valueType instanceof PrimitiveType) {
            return of(dataType);
        }
        else if (valueType instanceof CollectionType) {
            return this.getCollectionType(cmp, valueType, headers).pipe(map(collectionType => {
                dataType.valueType = collectionType;
                return dataType;
            }))

        }
        else {
            throw new Error(`ä¸æ¯æçåè¡¨é¡¹ç±»å: ${valueType.getKind()}`);
        }
    }


    private getStructureReferType(cmp: GspComponent, referType: StructureReferType, headers: HttpHeaders): Observable<StructuredType> {
        let refInfo: RefStructureInfo = cmp.refStructures.find(x => x.id == referType.referId);
        return this.structureSvc.get(refInfo.referType, refInfo.referId, headers).pipe(map(structureObj => {
            //ä½¿ç¨é»è®¤serializeContextè¿è¡ååºåå
            let serializeCtxt = new DefaultSerializeContext();
            let kind = structureObj['kind'];
            let serializer = serializeCtxt.getStructureSerializerFactory().get(kind);
            let structure = serializer.deserialize(structureObj, serializeCtxt) as CommonStructure;
            let structuredTypeId = refInfo.structuredTypeId;
            let allStructures = this.getAllStructures(structure);
            let referType = allStructures.find(x => x.id == structuredTypeId);
            return referType;
        }));
    }

    private getAllStructures(structure: CommonStructure) {
        let array: Array<StructuredType> = new Array<StructuredType>();
        if (structure.structuredTypes != null) {
            array = array.concat(structure.structuredTypes);
        }
        if (structure.refStructures != null) {
            structure.refStructures.forEach(refStructure => {
                if (refStructure.structuredTypes != null) {
                    array = array.concat(refStructure.structuredTypes);
                }
            })
        }
        return array;
    }


}