import { IDataTypeSerializer, ISerializeContext, IDataType } from '@ecp-caf/common-structure';
import { StructureReferTypeConvertor } from '../../../convertor/data-type/structure-refer-type-convertor';
import { StructureReferType } from '../../../define/data-type/structure-refer-type';

export class DefaultGspComponentDataTypeSerializer implements IDataTypeSerializer {
    serialize(type: IDataType, ctxt: ISerializeContext): Object {
        let result = new Object();
        let baseConvertor = new StructureReferTypeConvertor();
        baseConvertor.convertToObject(result, type, ctxt);
        return result;
    }

    deserialize(obj: Object, ctxt: ISerializeContext): IDataType {
        let result = new StructureReferType();
        let baseConvertor = new StructureReferTypeConvertor();
        baseConvertor.initFromObject(result, obj, ctxt);
        return result;
    }


}